/**
 * 
 * 1.css
 * 2.
 * 3.
 */
export default function removeAttrs(elem: Element, attrs: Array<string>): void {
    let i = 0;
    const { length } = attrs;
    for (; i < length; i++) {
        const attr = attrs[i];
        elem.getAttribute(attr) || elem.getAttribute(attr) === ''
            ? elem.removeAttribute(attr)
            : null;
    }
}
